<template>
  <div class="alert alert-primary">
    <h3> : {{ stats.success }}</h3>
    <h3> : {{ stats.error }}</h3>
    <h5 v-if="stats.success != 3">          !</h5>
    <h5 v-else>, !      !!</h5>
    <hr>
    <button class="btn btn primary" @click="$emit('repeat')"></button>
    <button class="btn btn primary" @click="$emit('nextLevel')" v-if="stats.success == 3"> </button>
    <v-playback :url="url" ></v-playback>
  </div>
</template>

<script>
  export default {
    props: ['stats'],
    url: 'https://www.youtube.com/watch?v=Chw1si91WaI'
  }
</script>

<style>

</style>



